/*
 ** 处理顶部导航的action
 ** @param state{object} 状态
 ** @param action(string) action事件
 */
import {
	fromJS,
} from 'immutable'
import {
	HEADLINE_ONCLICK,
	GET_HEADLINE,
	HEADLINE_SLIDE,
	GET_LAST_LIST,
	GET_HOTLIST,
	GET_ONCLICK_LIST,
	UPLOAD_PIC_WALL,
	GET_PIC_WALL
} from './action'
import {
	getHeadline
} from './../../modules/datas';
import {
	combineReducers
} from 'redux'
import cloneDeep from 'lodash/cloneDeep'
import options from './../../modules/option'
const domain = 'http://www.subui.com';

const checkImg = (imgurl) => {
	if (!imgurl) {
		return false
	}
	return (new RegExp(domain)).test(imgurl) ? imgurl : `${domain}${imgurl}`
}
const checkSrc = (item) => {
	if (options.staticUrl) {
		return (new RegExp(domain)).test(item.titleurl) ? item.titleurl : `${domain}${item.titleurl}`
	} else {
		return `/${item.classpath}/${item.classid}/${item.id}`;
	}
}
	// 处理幻灯图需要的数据
const renderSlide = (data) => {
	if (!data || !data.length) {
		return data;
	}
	console.log(data);
	const bond = {
		result: [],
		width: 640,
		interval: 400000,
		height: 320,
		currentIndex: 0,
		slideLength: 0,
	};
	data.map(function(item, index) {
		if (item.titlepic) {
			bond.result.push({
				src: checkSrc(item),
				imgUrl: checkImg(item.titlepic),
				title: item.title,
				active: index === 0 ? true : false,
			});
			bond.slideLength++;
		}
		return item;
	})
	return fromJS(bond);
};

function onclicklistReducer(state=fromJS([]),action){
	switch (action.type) {
		case GET_ONCLICK_LIST + '_SUCCESS':
			// 点击排行
			let onclickResult = action.payload.data,
				onclickList = [];
			if (!onclickResult.length) {
				return state
			};
			onclickResult.map((item, index) => {
				onclickList.push({
					title: item.title,
					classid: item.classid,
					id: item.id,
					src: checkSrc(item),
					imgUrl: checkImg(item.titlepic),
					desc: item.smalltext,
					sort: true
				})
			})
			return fromJS(onclickList);
		default:
			return state;
	}
}
function hotlistReducer(state = fromJS([]), action) {
	switch (action.type) {
		case GET_HOTLIST + '_SUCCESS':
			// 今日要闻
			console.log(action.payload)
			let hotResult = action.payload.data,
				hotlist = [];
			if (!hotResult.length) {
				return state
			};
			hotResult.map((item, index) => {
				hotlist.push({
					title: item.title,
					classid: item.classid,
					id: item.id,
					src: checkSrc(item),
					imgUrl: checkImg(item.titlepic),
					desc: item.smalltext
				})
			})
			return fromJS(hotlist);
		default:
			return state;
	}
}

function newslistReducer(state = fromJS([]), action) {
	switch (action.type) {
		case GET_LAST_LIST + '_SUCCESS':
			console.log(action)
			let newsResult = action.payload.data,
				newslist = [];
			if (newsResult && !newsResult.length) {
				return state
			};
			newsResult.map((item, index) => {
				newslist.push({
					title: item.title,
					classid: item.classid,
					id: item.id,
					src: checkSrc(item),
					imgUrl: checkImg(item.titlepic),
				})
			})
			return fromJS(newslist);
		default:
			return state;
	}
}

function headlineReducer(state = renderSlide(getHeadline.data), action) {
	switch (action.type) {
		case GET_HEADLINE + '_SUCCESS':
			// 幻灯图抓取成功
			const result = action.payload.result && action.payload.result.length ? action.payload.data : [];
			return renderSlide(result);
		case HEADLINE_ONCLICK:
			// 幻灯图点击切换
			return state.set('currentIndex', +action.payload.index)

		case HEADLINE_SLIDE:
			// 处理幻灯图的自动播放
			var newS = state.toJS().headline,
				imgCount = newS.slideLength,
				curIndex = newS.currentIndex;
			let nextIndex = (curIndex + 1) >= imgCount ? 0 : curIndex + 1;
			return state.set('currentIndex', nextIndex);
		default:
			return state;
	}
}

function picWallReducer(state = fromJS([]), action) {
	switch (action.type) {
		case GET_PIC_WALL + '_SUCCESS':
			// 图片墙
			let picResult = action.payload.data,
				picwall = [];
			if (!picResult.length) {
				return state
			};
			picResult.map((item, index) => {
				picwall.push({
					title: item.title,
					classid: item.classid,
					id: item.id,
					src: checkSrc(item),
					imgUrl: checkImg(item.titlepic),
					desc: item.smalltext,
					styles: {},
				})
			})
			return fromJS(picwall);
		case UPLOAD_PIC_WALL:
			// 更新图片墙
			return state.setIn([+action.payload.index, 'styles'], action.payload.styles);
		default:
			return state;
	}
}
export default combineReducers({
	picwall: picWallReducer,
	headline: headlineReducer,
	newslist: newslistReducer,
	onclicklist: onclicklistReducer,
	hotlist: hotlistReducer
});


